},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"bloque-texto-g__texto p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("El costo de los inventarios puede no ser recuperable en caso de que los mismos estén dañados, si han devenido parcial o totalmente obsoletos, o bien si sus precios de mercado han caído. Asimismo, el costo de los inventarios puede no ser recuperable si los costos estimados para su terminación o su venta han aumentado. La práctica de rebajar el saldo, hasta que el costo sea igual al valor neto realizable, es coherente con el punto de vista según el cual los activos no deben registrarse en libros por encima de los importes que se espera obtener a través de su venta o uso.")])])
