},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-6 mb-lg-0 mb-3"},[_c('div',{staticClass:"tarjeta p-4 h-100",staticStyle:{"background-color":"#DDEFFD"}},[_c('p',{staticClass:"mb-0"},[_c('b',[_vm._v("Objetivo:")]),_vm._v(" El objetivo de esta norma es prescribir el tratamiento contable de los inventarios. Un tema fundamental en la contabilidad de los inventarios es la cantidad de costo que debe reconocerse como un activo, para que sea diferido hasta que los ingresos correspondientes sean reconocidos. Esta norma suministra una guía práctica para la determinación de ese costo, así como para el subsiguiente reconocimiento como un gasto del periodo, incluyendo también cualquier deterioro que rebaje el importe en libros al valor neto realizable. También suministra directrices sobre las fórmulas del costo que se usan para atribuir costos a los inventarios (NIC, s.f., pp. 4-16).")])])]),_c('div',{staticClass:"col-lg-6 mb-lg-0 mb-3"},[_c('div',{staticClass:"tarjeta p-4 h-100",staticStyle:{"background-color":"#F3F0EA"}},[_c('p',{staticClass:"mb-0"},[_c('b',[_vm._v("Alcance:")]),_vm._v(" Esta norma se aplica a todos los inventarios, excepto a: (a) [eliminado] (b) los instrumentos financieros (véanse las NIC 32 Instrumentos financieros: presentación y NIIF 9 Instrumentos financieros); y (c) los activos biológicos relacionados con la actividad agrícola y productos agrícolas en el punto de cosecha o recolección (ver la NIC 41 Agricultura)."),_c('br'),_c('br'),_vm._v("Esta norma no se aplica en la medición de los inventarios mantenidos por: a) Productores de productos agrícolas y forestales, de productos agrícolas tras la cosecha o recolección, de minerales y de productos minerales, siempre que sean medidos por su valor neto realizable, de acuerdo con prácticas bien consolidadas en esos sectores industriales.")])])])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-7 mb-lg-0 mb-3"},[_c('p',{staticClass:"mb-0"},[_vm._v("En el caso de que esos inventarios se midan al valor neto realizable, los cambios en este valor se reconocerán en el resultado del periodo en que se produzcan dichos cambios. b) Intermediarios que comercian con materias primas cotizadas, siempre que midan sus inventarios al valor razonable menos costos de venta. En el caso de que esos inventarios se lleven al valor razonable menos costos de venta, los cambios en el importe del valor razonable menos costos de venta se reconocerán en el resultado del periodo en que se produzcan dichos cambios."),_c('br'),_c('br'),_vm._v("Los inventarios a que se ha hecho referencia se miden por su valor neto realizable en ciertas fases de la producción. Ello ocurre, por ejemplo, cuando se han recogido las cosechas agrícolas o se han extraído los minerales, siempre que su venta esté asegurada por un contrato a plazo sea cual fuere su tipo o garantizada por el gobierno, o bien cuando existe un mercado activo y el riesgo de fracasar en la venta sea mínimo. Esos inventarios 1. 2. 3. 4. NIC 2 A1062 © IFRS Foundation se excluyen únicamente de los requerimientos de medición establecidos en esta Norma.")])]),_c('div',{staticClass:"col-lg-5 col-7"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/26.png"),"alt":""}})])])
